import { Injectable, Inject, PLATFORM_ID } from '@angular/core';
import { isPlatformServer } from '@angular/common';

export interface USER_DATA {
  // user_id:number,
  // user_type:string,
  // user_session:string,
  // auth_type:number,
  // auth_id:string,
  // user_nm:string,
  // company_name:string,
  // push_state:string

  user_id:number, // ID			
  user_session:string, // 
  user_type:string, // ADMIN -  / USER - 			
  user_level:number, // 0  / 1 			
  auth_type:number, // 1			
  auth_id:string, // 			
  user_name:string, // 			
  user_phone:string, // 			
  user_email:string, // 			
  user_scene:string, // 			
  user_birthday:string, // 			
  user_address:string, // 			
  user_address_detail:string, // 			
  user_nationality:string, // 			
  user_foreigner_no:string, // 			
  user_sos_name:string, // 			
  user_sos_phone:string, // 			
  user_sos_family:string, // 			
  edu_date:string,
  company_id:number, // ID			
  company_name:string, // 			
  company_type_name:string, // 			
  company_duty_name:string, // 			
  company_position_name:string, // 			
  construction_name:string, // 			
  occupation_name:string, // 			
  company_duty_name_etc:string, //  			
  company_position_name_etc:string, //  			
  construction_name_etc:string, //  			
  occupation_name_etc:string, //  			
  user_safe_file:string, // 			
  user_sign_file:string, // 			
  work_permission:boolean, /// true -  			
  user_profile_file:string, // 			
  bicon_rssi:string, //  		
  bicon_state: boolean, // true -        	
  push_state,
  alarm_data,
  user_license_state:boolean //  
}

@Injectable({
  providedIn: 'root'
})
export class StorageService {

  constructor(
    @Inject(PLATFORM_ID) private platformId
  ) {
    this.user.get();
  }

  name = 'kunyoung-app@';

  user = {
    auto_login: false,
    data: {
      // user_id: 0,
      // user_type: '',
      // user_session: '',
      // auth_type: 0,
      // auth_id: '',
      // user_nm: '',
      // company_name: '',
      // push_state: ''

      user_id: 0, // ID			
      user_session: '', // 
      user_type: '', // ADMIN -  / USER - 			
      user_level: 0, // 0  / 1 			
      auth_type: 1, // 1			
      auth_id: '', // 			
      user_name: '', // 			
      user_phone: '', // 			
      user_email: '', // 			
      user_scene: '', // 			
      user_birthday: '', // 			
      user_address: '', // 			
      edu_date: '', //    
      user_address_detail	: '', // 			
      user_nationality: '', // 			
      user_foreigner_no: '', // 			
      user_sos_name: '', // 			
      user_sos_phone: '', // 			
      user_sos_family: '', // 			
      company_id: 0, // ID			
      company_name: '', // 			
      company_type_name: '', // 			
      company_duty_name: '', // 			
      company_position_name: '', // 			
      construction_name: '', // 			
      occupation_name: '', // 			
      company_duty_name_etc: '', //  			
      company_position_name_etc: '', //  			
      construction_name_etc: '', //  			
      occupation_name_etc: '', //  			
      user_safe_file: '', // 			
      user_sign_file: '', // 			
      work_permission: false, // true -  			
      user_profile_file: '', // 			
      bicon_rssi: '', //  	
      bicon_state: false, // true -        	
      push_state: '',
      alarm_data: {
        alarm_count: 0,
        notice_count: 0
      }
    } as USER_DATA,
    get: () => {
      if(!isPlatformServer(this.platformId)) {

        const first_key = this.name + Object.keys(this.user.data)[0];
        const storage = window.sessionStorage.getItem(first_key) ? 'sessionStorage' :
        window.localStorage.getItem(first_key) ? 'localStorage' : '';
        if(storage === 'localStorage') this.user.auto_login = true;
        if(storage) {
          for(let key in this.user.data) {
            switch(typeof this.user.data[key]) {
              case 'string':
                this.user.data[key] = window[storage].getItem(this.name + key);
                break;
              case 'number':
                this.user.data[key] = Number(window[storage].getItem(this.name + key));
                break;
              case 'boolean':
                this.user.data[key] = Boolean(window[storage].getItem(this.name + key));
                break;
            }
          }
        } else {
          for(let key in this.user.data) {
            switch(typeof this.user.data[key]) {
              case 'string':
                this.user.data[key] = '';
                break;
              case 'number':
                this.user.data[key] = 0;
                break;
            }
          }
        }
      }
      return this.user.data;
    },
    set: (data:USER_DATA, auto:boolean = true) => {
      if(location.href.indexOf('employee-app/signup') == -1) this.user.clear();
      if(!isPlatformServer(this.platformId)) {
        let storage:string = auto ? 'localStorage' : 'sessionStorage';
        for(let key in this.user.data) {
          window[storage].setItem(this.name + key, data[key] || this.user.data[key]);
        }
      }
      this.user.get();
    },
    clear: () => {
      if(!isPlatformServer(this.platformId)) {
        for(let key in this.user.data) {
          window.sessionStorage.removeItem(this.name + key);
          window.localStorage.removeItem(this.name + key);
          sessionStorage.removeItem('hr_session');
        }
        this.user.get();
      }
    }
  }
}